*{
    padding: 0;
    padding: 0;
    font-size: large;
}

.logo{
    background: #ffffff;
}

.wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 2vh;
    padding-bottom: 2vh;
    padding-left: 5vh;
    background-color: white;
    box-shadow: 0px 4px 5px -3px #888;
    
}

.links button{

    height: 4vh;
    width: 15vh;
    padding-left: 10px;
    padding-right: 10px;
    border: none;
    background: white;
    
}
.icons button{
    height: 5vh;
    width: 16vh;

    border-radius: 8px;
    padding-left: 4vh;
    padding-right: 4vh;
    color: #194D79;
    border-color: #194D79;
    background-color: white;
    font-size: large;
}

.links button:hover{
    border-bottom: 3px solid #89C400;
}
.links{
    display: flex;
    justify-content: space-evenly;
}